.movie-card {
    position: relative;
    width: 250px;
    margin: 15px;
    cursor: pointer;
    transition: transform 0.3s;
  }
  
  .movie-card:hover {
    transform: scale(1.05) translateY(-5px);
    /* 박스 그림자?*/
  }
  
  .movie-poster {
    width: 100%;
    height: auto;
    border-radius: 5px;
  }
  
  .movie-title {
    margin-top: 10px;
    font-size: 16px;
    text-align: center;
    /*색상?*/
  }
  
  .wishlist-indicator {
    position: absolute;
    top: 10px;
    right: 10px;
    background-color: rgba(255, 255, 255, 0.7);
    border-radius: 50%;
    padding: 5px;
  }

  /* 위시리스트 버튼 추가? */