.modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
  }
  
  .modal-content {
    background: white;
    padding: 20px;
    border-radius: 5px;
    width: 500px;
    max-width: 90%;
    max-height: 80vh;
    overflow-y: auto;
  }
  
  .modal-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 15px;
  }
  
  .close-button {
    background: none;
    border: none;
    font-size: 20px;
    cursor: pointer;
  }
  
  .terms-content {
    color: #000;
    font-size: 14px;
    line-height: 1.6;
    padding: 10px;
  }
  
  .terms-content h5 {
    color: #000;
    margin-bottom: 15px;
    font-weight: bold;
  }
  
  .terms-content p {
    color: #000;
    margin-bottom: 10px;
  }
  
  .modal-footer {
    margin-top: 20px;
    display: flex;
    justify-content: center;
  }
  
  .agree-button {
    background: #007bff;
    color: white;
    border: none;
    padding: 8px 16px;
    border-radius: 4px;
    cursor: pointer;
    width: 100px;
  }
  
  .agree-button:hover {
    background: #0056b3;
  }