.loading-spinner {
    /*position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;*/
    display: flex;
    justify-content: center;
    align-items: center;
    /*padding: 20px*/
    background-color: rgba(255, 255, 255, 0.8); /* ?*/
    z-index: 1000; /*필요한가?*/
  }
  
  .spinner {
    border: 8px solid rgba(0, 0, 0, 0.1);
    border-top: 8px solid #333;
    border-radius: 50%;
    width: 50px;
    height: 0px;
    animation: spin 1s linear infinite;
  }
  
  @keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
  }